body {
  background-color: #dcdcdc;
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
  overflow: hidden;
}

main {
  min-height: 100dvh;
  place-content: center;
  display: grid;
}

h1 {
  color: #d8bfd8;
  text-transform: uppercase;
  font-size: 7vw;
  font-weight: 800;
  animation: 1.8s infinite pulse;
}

@keyframes pulse {
  0% {
    transform: scale(.8);
  }

  5% {
    opacity: .5;
    transform: scale(.9);
  }

  10% {
    transform: scale(.8);
  }

  15% {
    transform: scale(1);
  }

  50% {
    opacity: .8;
    transform: scale(.8);
  }

  100% {
    transform: scale(.8);
  }
}

